/* https://codepen.io/gabrielcarol/pen/rGeEbY */
.keys {
  display: block;
  width: 100%;
  height: 350px;
  max-width: 880px;
  position: relative;
  margin: 40px auto 0;
}

.key {
  position: relative;
  border: 4px solid black;
  border-radius: .5rem;
  display: block;
  box-sizing: border-box;
  z-index: 2;
}

.key:not(.sharp) {
  float: left;
  width: 10%;
  height: 100%;
  background: rgba(255, 255, 255, .8);    
}

.key.sharp {
  position: absolute;
  width: 6%;
  height: 60%;
  background: #000;
  color: #eee;
  top: 0;
  z-index: 3;
}

.key[data-key="87"] {
  left: 7%;
}

.key[data-key="69"] {
  left: 17%;
}

.key[data-key="84"]  {
  left: 37%;
}

.key[data-key="89"] {
  left: 47%;
}

.key[data-key="85"] {
  left: 57%;    
}

.key[data-key="79"] {
  left: 77%;    
}

.key[data-key="80"] {
  left: 87%;    
}

.playing {
  transform: scale(.95);
  border-color: #028ae9;
  box-shadow: 0 0 1rem #028ae9;

}

.hints {
  display: block;
  width: 100%;
  opacity: 0;
  display: none;
  position: absolute;
  bottom: 7px;
  transition: opacity .3s ease-out;
  font-size: 20px;
}
:root {
  --offset: 0deg;
}
.playing[data-note^="C"] {
  border-color: rgb(255, 0, 0);
  box-shadow: 0 0 1rem rgb(255, 0, 0);
}
.playing[data-note^="D"] {
  border-color: rgb(255, 255, 0);
  box-shadow: 0 0 1rem rgb(255, 255, 0);
}
.playing[data-note^="E"] {
  border-color: rgb(0, 255, 0);
  box-shadow: 0 0 1rem rgb(0, 255, 0);
}
.playing[data-note^="F"] {
  border-color: rgb(0, 255, 255);
  box-shadow: 0 0 1rem rgb(0, 255, 255);
}
.playing[data-note^="G"] {
  border-color:rgb(0, 0, 255);
  box-shadow: 0 0 1rem rgb(0, 0, 255);
}
.playing[data-note^="A"] {
  border-color: rgb(123, 0, 255);
  box-shadow: 0 0 1rem rgb(123, 0, 255);
}
.playing[data-note^="B"] {
  border-color: rgb(255, 0, 255);
  box-shadow: 0 0 1rem rgb(255, 0, 255);

}
